<template>
  <div class="su-container">
    <div v-for="i in 9" :key="i" class="su-row">
      <div v-for="j in 9" :key="j" class="num-cell">
        <button
          v-if="sudokuGrid[(i - 1) * 9 + j - 1] != 0"
          class="su-num su-original"
          :class="getBorder(i, j)"
          type="button"
        >
          {{ sudokuGrid[(i - 1) * 9 + j - 1] }}
        </button>
        <button
          v-else
          class="su-num su-empty"
          type="button"
          :class="getBorder(i, j)"
        ></button>
      </div>
    </div>
  </div>
</template>
<script setup>
import { computed, defineProps } from "vue";
const props = defineProps({
  sudokuGrid: {
    type: Array,
    required: true,
    default: () => [0] * 9,
  },
});
const sudokuGrid = computed(() => props.sudokuGrid);
function getBorder(i, j) {
  if (i % 3 == 0 && j % 3 == 0 && j != 9 && i != 9) {
    return "su-border-bottom su-border-right";
  } else if (i % 3 == 0 && i != 9) {
    return "su-border-bottom";
  } else if (j % 3 == 0 && j != 9) {
    return "su-border-right";
  } else {
    return "";
  }
}
</script>
<style lang="scss" scoped>
.su-container {
  width: 30vmax;
  height: 30vmax;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: column nowrap;
}
.su-row {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: row nowrap;
  width: 100%;
  height: 100%;
}
.num-cell {
  flex: 1;
  width: 100%;
  height: 100%;
}
.su-num {
  width: 100%;
  height: 100%;
  border: 1px solid rgb(86, 86, 86);
  background-color: #cccccc;
  &:hover {
    background-color: #3982e0;
  }
}
.su-empty {
  background-color: #eaeaea;
}
.su-border-bottom {
  border-bottom: 3px solid rgb(86, 86, 86);
}
.su-border-right {
  border-right: 3px solid rgb(86, 86, 86);
}
</style>
